@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
#searchBar {
    font-family: 'Open Sans';
    border: solid;
    border-width: .5px;
    width: 250px;
    height: 25px;
    position: relative;
    transition: background .2s ease-in;
}

#displayText {
    padding-left: 2px;
}

.greyBackground {
    position: absolute;
    right: 0px;
    top: 0px;
    width:25px;
    height:25px;
    background-color:#DCDCDC;
}

.searchIcon {
    position: absolute;
    top: 5px;
    right: 3.5px;
}

.searchBarContainer {
    padding-top: 30px;
}

#searchBar:hover {
    transition: background .5s ease-in;
    background-color:#F8F8F8;
    cursor: pointer;
}
.centerContainer {
    display:flex;
    justify-content: center;
}

.pageContainer {
    position:absolute;
    top:170px;
    width: 500px;;
}
#dropdownContainer {
    position:absolute;
    display:none;
    top:64px;
    border: solid;
    font-family: 'Open Sans';
    border-width: .5px;
    width: 250px;
    font-size: 20px;
}

.dropdownItem {
    font-family: 'Open Sans';
    padding-left: 2px;
    transition: background .2s ease-in;
}

.dropdownItem:hover {
    transition: background .5s ease-in;
    cursor: pointer;
}

.dropdownItem:nth-child(1):hover {
    background-color:#ffcccc;
}

.dropdownItem:nth-child(2):hover {
    background-color:#ccffdd;
}

.dropdownItem:nth-child(3):hover {
    background-color: #ccccff;
}
.homePageBackground {
    background-color: #ffcccc;
    font-family: 'Open Sans';
}

.information {
    margin-left: 10px;
    margin-right: 10px;
}

.subtextProfile {
    font-size: 8px;
    margin-left: 25%;
}

#profile {
    margin-top:10px;
    border-radius: 25px;
    margin-left: 25%;
    width: 250px;
}
